@import"https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap";*{margin:0;padding:0;box-sizing:border-box}input,button,textarea,select{margin:0;font:inherit}.app.dark{--bg-color: #282c34;--font-color: #fff;--link-color-primary: #09d3ac;--header-bg: #18191a;--input-bg: #3b3e47;--input-bg-hover: #212329;--violet-color: #818CF8}.app.light{--bg-color: #ffffff;--font-color: #0e0e0e;--link-color-primary: #09d3ac;--header-bg: #e4e4e4;--input-bg: #ffffff;--input-bg-hover: #abb8dd;--violet-color: #818CF8}.app{background-color:var(--bg-color);color:var(--font-color);min-height:100vh;font-family:"Montserrat"}.app a{color:var(--link-color-primary)}.page-center{display:flex;flex-direction:column;align-items:center}.submit{border:2px;background-color:var(--input-bg);border-radius:6px;border:2px solid var(--violet-color);color:var(--violet-color);font-weight:600;padding:7px;cursor:pointer;transition:all .1s}.submit:hover{background-color:var(--input-bg-hover)}.main{padding:0 150px}.theme-switch__input,.theme-switch__label{position:absolute;z-index:1}.theme-switch__input{opacity:0}.theme-switch__input:hover+.theme-switch__label,.theme-switch__input:focus+.theme-switch__label{background-color:#789}.theme-switch__input:hover+.theme-switch__label span::after,.theme-switch__input:focus+.theme-switch__label span::after{background-color:#d4ebf2}.theme-switch__label{transition:background-color 200ms ease-in-out;width:110px;height:40px;border-radius:50px;text-align:center;background-color:#708090;box-shadow:-4px 4px 15px inset rgba(0,0,0,.4);transform:translate(48px, -10px)}.theme-switch__label::before,.theme-switch__label::after{font-size:2rem;position:absolute;transform:translate3d(0, -50%, 0);top:50%}.theme-switch__label::before{content:"☼";right:100%;margin-right:10px;color:orange}.theme-switch__label::after{content:"☾";left:100%;margin-left:10px;color:#789;transform:translateY(-24px)}.theme-switch__label span{position:absolute;bottom:calc(100% + 10px);left:0;width:100%}.theme-switch__label span::after{position:absolute;top:calc(100% + 15px);left:5px;width:30px;height:30px;content:"";border-radius:50%;background-color:#add8e6;transition:transform 200ms,background-color 200ms;box-shadow:-3px 3px 8px rgba(0,0,0,.4)}.theme-switch__input:checked~.theme-switch__label{background-color:#789}.theme-switch__input:checked~.theme-switch__label::before{color:#789}.theme-switch__input:checked~.theme-switch__label::after{color:var(--violet-color)}.theme-switch__input:checked~.theme-switch__label span::after{transform:translate3d(70px, 0, 0)}